.films-list{
    padding-bottom: 20px;
}

.films-list__film{
    width: 50%;
    outline: 1px solid #808080;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 0 auto;
    padding-left:1%;
    margin-bottom: 10px;
}

.films-list__film-name{
    color: #000000;
    font-size: 14px;
    width: 65%;
}

.films-list__add-bookmarks{
    width: 50px;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    outline: 1px solid #808080;
}

.films-list__add-bookmarks-star{
    width: 50%;
}

.films-list__bookmarks .films-list__add-bookmarks-star polygon{
    fill:#FFFF00;
}

.films-list__show-more{
    height: 30px;
    width: 120px;
    outline: 1px solid #808080;
    font-size: 14px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #000000;
    margin: 0 auto;
}