.header{
    padding-top: 50px;
}

.header__navigation{
    margin: 0 auto;
    display: flex;
    width: 250px;
    justify-content: space-between;
}

.header__navigation-item{
    height: 30px;
    width: 120px;
    outline: 1px solid #808080;
    font-size: 14px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #000000;
}

.header__navigation-item:hover{
    background-color:#D3D3D3;
}

.header__navigation-active-item{
    background-color: #808080;
}