.search-block{
    padding-top: 30px;
}

.search-block_not-active{
    display: none;
}

.search-block__search-field{
    margin: 0 auto;
    display: block;
    width: 40%;
    height: 35px;
    padding-left: 10px;
    padding-right: 10px;
    margin-bottom: 20px;
}

.search-block__search-field:focus{
    outline: none;
}

.search-block__tags{
    width: 60%;
    margin: 0 auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-bottom: 20px;
}

.search-block__tag{
    height: 25px;
    width: auto;
    padding-left: 10px;
    padding-right: 10px;
    outline: 1px solid #808080;
    font-size: 12px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #000000;
    margin-bottom: 10px;
}

.search-block__active-tag{
    background-color:#808080;
}
